* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  //   border: 1px solid black;
}
.main-container {
  // border: 2px solid red;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  .body-color {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 90%;
    width: 100%;
    text-align: center;
    align-self: center;
    p {
      font-size: 29px;
      font-weight: bold;
      color: rgb(15, 107, 123);
    }
  }
  button {
    padding: 16px;
    margin: 10px;
    border: none;
    border-radius: 5px;
    font-size: 11px;
    background-color: rgb(254, 255, 255);
    color: rgb(34, 203, 203);
    font-weight: bolder;
    box-shadow: 1px 1px 8px 1px rgba(0, 0, 0, 0.268);
    cursor: pointer;
  }
}
@media screen and (min-width: 510px) {
  .main-container {
    .body-color {
      p {
        font-size: 90px;
      }
    }
    button {
      font-size: 17px;
    }
  }
}
